<template>
<div>
    <header-client></header-client>

                <!-- Page Content -->
                <div style="margin-top:-500px;" class="align-items-center">
                    <div class="w-100">
                        <!-- Reminder Section -->
                        <div class="bg-white">
                            <div class="content content-full">
                                <div class="row justify-content-center">
                                    <div class="col-md-8 col-lg-6 col-xl-4 py-4">
                                        <!-- Header -->
                                        <div class="text-center">
                                            <p class="mb-2">
                                                <i class="fa fa-2x fa-circle-notch text-primary"></i>
                                            </p>
                                            <h1 class="h4 mb-1">
                                                Password Reminder
                                            </h1>
                                            <h2 class="h6 font-w400 text-muted mb-3">
                                                Please provide your accounts email and we will send you your password.
                                            </h2>
                                        </div>
                                        <!-- END Header -->

                                        <!-- Reminder Form -->
                                        <!-- jQuery Validation (.js-validation-reminder class is initialized in js/pages/op_auth_reminder.min.js which was auto compiled from _es6/pages/op_auth_reminder.js) -->
                                        <!-- For more info and examples you can check out https://github.com/jzaefferer/jquery-validation -->
                                        <form class="js-validation-reminder" action="be_pages_auth_all.html" method="POST" @submit.prevent="ForgotPassword">
                                            
                                            <div class="form-group py-3">
                                                <input type="text" :class="erremail?'invalid':'valid'" class="form-control form-control-lg form-control-alt" id="reminder-credential" name="email" v-model="form.email" placeholder="Email">
                                                <span v-if='errors.email' v-bind:style="error" class="error"><strong>{{errors.email}}</strong></span>
                                            </div>
                                            <div class="form-group row justify-content-center">
                                                <div class="col-md-6 col-xl-5">
                                                    <button type="submit" class="btn btn-block btn-primary">
                                                        <i class="fa fa-fw fa-envelope mr-1"></i> Send Mail
                                                    </button>
                                                </div>
                                            </div>
                                        </form>
                                        <!-- END Reminder Form -->

                                        <div class="text-center">
                                           <!-- <a class="font-size-sm font-w500" href="op_auth_signin2.html">Login?</a>-->
                                   <router-link to="/login" class="font-size-sm font-w500"> <span class="nav-main-link-name">Login?</span> </router-link>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!-- END Reminder Section -->

                        <!-- Footer -->
                        <div class="font-size-sm text-center text-muted py-3">
                            <strong>SpaLV 4.7</strong> &copy; <span data-toggle="year-copy"></span>
                        </div>
                        <!-- END Footer -->
                    </div>
                </div>
                <!-- END Page Content 

<footer-client></footer-client>-->
</div>




</template>

<script>
import firebase from 'firebase/app';
import $ from 'jquery'
    export default {
        data() {
        return {
            erremail:false,
            form: {
                email: '',
              
            },      
            errors:[],
            error: {
              color: '#e56767',
              fontSize: '.875rem',
              //verticalAlign:'-15px',
              
            },
           //  erremail:false,
        }
    },
        //probleme in home css 
        mounted() {
            this.$Progress.start();
            console.log('Component mounted.')
            this.$Progress.finish();
                $('#page-container').removeClass('sidebar-o sidebar-dark enable-page-overlay side-scroll page-header-fixed main-content-narrow')
        },methods:{
            
        ForgotPassword(){
            this.$Progress.start();

     if(this.form.email.length ==''){
           this.erremail=true;
           this.errors.email='The email field is required.';
    
            this.$Progress.fail();

       }else{
           this.erremail=false;
           this.errors.email='';
        

       }
        var auth = firebase.auth();
        var emailAddress = this.form.email;

        if(this.erremail==false){
            auth.sendPasswordResetEmail(emailAddress).then(()=>{
            this.$Progress.finish();
          $.notify({icon: 'fa fa-check mr-1', message: 'The email is send to your email for reset account password'},{type: 'success'}); 

                }).catch(err=> {
                console.log(err);
           // this.$Progress.fail();
            this.$Progress.fail();

           if(err.code=='auth/invalid-email'){

           this.errors.email='The email address is badly formatted.';
           this.erremail=true;
            }else if(err.code=='auth/user-not-found'){
                
           this.errors.email='There is no user record corresponding to this identifier.';
           this.erremail=true;
            }

                });
        }
        
            
            }
        }
    }
</script>
<style scoped>
.invalid{
    border-color: #fbeaea;
    background-color: #fbeaea;
}  
.valid{ 
    border-color: #edf0f2;
    background-color: #edf0f2;
} 
</style>