<template>
<div>
    <!-- <header-client ></header-client> -->

                <!-- Page Content -->
                <div class="align-items-center">
                   <div class="w-100">
                            <!-- Unlock Section -->
                            <div class="bg-white">
                                <div class="content content-full">
                                    <div class="row justify-content-center">
                                        <div class="col-md-8 col-lg-6 col-xl-4 py-4">
                                            <!-- Header -->
                                            <div class="text-center">
                                                <p class="mb-2">
                                                    <i class="fa fa-2x fa-circle-notch text-primary"></i>
                                                </p>
                                                <h1 class="h4 mb-1">
                                                    Account Locked
                                                </h1>
                                                <h2 class="h6 font-w400 text-muted mb-5">
                                                    Please enter your password to unlock your account
                                                </h2>
                                               <img class="img-avatar img-avatar96" :src="this.userinfo.image" alt="">
                                                <p class="font-w600 text-center my-2">
                                                    {{user.email}}
                                                </p>
                                            </div>
                                            <!-- END Header -->

                                            <!-- Unlock Form -->
                                            <!-- jQuery Validation (.js-validation-signin class is initialized in js/pages/op_auth_signin.min.js which was auto compiled from _es6/pages/op_auth_signin.js) -->
                                            <!-- For more info and examples you can check out https://github.com/jzaefferer/jquery-validation -->
                                            <form class="js-validation-lock" action="#"  @submit.prevent="unlockScreen">
                                                <div class="form-group py-3">
                                                    <input type="password"  :class="errpassword?'invalid':'valid'" class="form-control form-control-lg form-control-alt" v-model="form.password" id="lock-password" name="password" placeholder="Password..">
                                                    <span v-if='errors.password' v-bind:style="error" class="error">{{errors.password}}</span>
                                                </div>
                                                <div class="form-group row justify-content-center">
                                                    <div class="col-md-6 col-xl-5">
                                                        <button type="submit" class="btn btn-block btn-alt-primary">
                                                            <i class="fa fa-fw fa-lock-open mr-1"></i> Unlock
                                                        </button>
                                                    </div>
                                                </div>
                                            </form>
                                            <!-- END Unlock Form -->
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <!-- END Unlock Section -->
                               <!-- <router-link to="/home" class="nav-main-link"> <i class="nav-main-link-icon si si-home"></i> <span class="nav-main-link-name">Home</span> </router-link>-->

                            <!-- Footer -->
                            <div class="font-size-sm text-center text-muted py-4">
                             
                                <strong>OneUI 4.7</strong> &copy; <span data-toggle="year-copy"></span>
                            </div>
                            <!-- END Footer -->
                        </div>
                </div>
                <!-- END Page Content 

<footer-client></footer-client>-->
</div>




</template>

<script>
import $ from 'jquery'
import axios from 'axios'
import firebase from 'firebase/app'


    export default {
             data() {
        return {
            errpassword:false,
     form:{
        password:'',
    },
    errors:[],
    error: {
        color: '#e56767',
        fontSize: '.875rem',    
    },
        user:{},
        userinfo:{ 
        image:''
        },


        }
    },
        //probleme in home css 
        mounted() {
            this.$Progress.start();
            console.log('Component mounted.');
              localStorage.setItem('lock_screen',true);

              firebase.auth().onAuthStateChanged((user)=>{
                if((user && firebase.auth().currentUser.emailVerified)){
                 //alert(user.photoURL);
                  this.user=user;
                    if(user.photoURL==null || user.photoURL=='avatar13.jpg'){
                      //  this.userinfo.image='https://firebasestorage.googleapis.com/v0/b/laravelvueoneui.appspot.com/o/img.jpg?alt=media&token=00b3c01a-3f57-41c8-8ebd-995c7baa4e2b'

                 firebase.storage().ref().child(`avatar13.jpg`).getDownloadURL().then((imagUrl)=>{
                        this.userinfo.image=imagUrl
                       //alert(imagUrl)
                    }).catch(err=>{
                        console.log(err);
                    })

                    }else{

                      firebase.storage().ref().child(`users/+${this.user.uid}+/${this.user.photoURL}`).getDownloadURL().then(imagUrl=>{
                        this.userinfo.image=imagUrl
                       //alert(imagUrl)
                    }).catch(err=>{
                        console.log(err);
                    })

                    }
          
                }else {
                         this.user = null;
            }
            })
            this.$Progress.finish();
                $('#page-container').removeClass('sidebar-o sidebar-dark enable-page-overlay side-scroll page-header-fixed main-content-narrow')
        },
        
        methods:{
            unlockScreen(){
            this.$Progress.start();

            if(this.form.password.length==''){
                    this.errpassword=true;
                    this.errors.password="The password field is required.";
                    this.$Progress.fail();

                }else{
                     this.errpassword=false;
                    this.errors.password="";
                }


 var user = firebase.auth().currentUser;
 const cred = firebase.auth.EmailAuthProvider.credential(user.email, this.form.password);
//alert(user.email);
user.reauthenticateWithCredential(cred).then(()=> {
 this.$Progress.finish();

      localStorage.removeItem('lock_screen');
      this.$router.go(-1);
      $.notify({icon: 'fa fa-user mr-1', message: 'Welcome to your account'},{type: 'success'});
      this.form.password="";
      this.errors.password="";
}).catch(error=> {
  console.log(error);
  if(error.code=='auth/wrong-password'){
      this.errpassword=true;
      this.errors.password='The password is invalid'
  }
            this.$Progress.fail();

});


        }
        },
       /*         beforeRouteLeave (to, from, next) {
          const answer = window.confirm('Do you really want to leave? you have unsaved changes!')
            if (answer) {
              next()
            } else {
              next(false)
            }
             if(localStorage.unlock_account===null){
                next()
            }else{
                next(false);
            }
          }*/
    }
</script>
<style scoped>
.invalid{
  /*  border-color: #fbeaea;*/
    background-color: #fbeaea;
    border-color: #f4c6c6;

}  
.valid{ 
    border-color: #d5dce1;
    background-color: #edf0f2;
}
</style>
